<template>
  <div class="address">
    <m-select hintText="" v-model="myprovince" maxOptionHeight="300px" v-if="provinceState">
      <m-option v-for="item, index in address" :key="index" :value="item.province">{{ item.province }}</m-option>
    </m-select>

    <m-select hintText="" v-model="mycity" maxOptionHeight="300px" :item="choose_city" v-if="provinceState && cityState">
      <m-option v-for="c, cid in choose_city" :key="cid" :value="c">{{ c }}</m-option>
    </m-select>

    <m-input hintText="" v-model="mycity" @input="handleInput(arguments[0])" v-if="!provinceState && cityState"></m-input>
    
    <m-input hintText="" full v-model="mytext" @input="handleInput(arguments[0])"></m-input>
  </div>
</template>

<script>
// import $ from 'webpack-zepto'
let adderssData = [
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
