    'city': ['', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '']
  },
  {
    'province': ' ',
    'city': ['', '', '', '', '']
  },
  {
    'province': '',
    'city': ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
  },
