    'city': ['']
  }
]
export default {
  name: 'PAddress',
  props: {
    option: Object,
    answers: Object
  },
  data () {
    return {
      address: adderssData,
      provinceState: true,
      cityState: true,
      myprovince: '',
      mycity: '',
      mytext: ''
    }
  },
  computed: {
    choose_city: function () {
      let mycity = ['']
      this.address.map(city => {
        if (this.myprovince === city.province) {
          mycity = city.city
        }
      })
      return mycity
    }
  },
  mounted () {
    if (this.option) {
      this.provinceState = this.option.province
      this.cityState = this.option.city
    }
    if (this.answers) {
      this.myprovince = this.answers.province
      this.mycity = this.answers.city
      this.mytext = this.answers.text
    }
  },
  watch: {
    myprovince () {
      this.handleChange()
    },
    mycity () {
      this.handleChange()
    },
    answers () {
      this.myprovince = this.answers.province
      this.mycity = this.answers.city
      this.mytext = this.answers.text
    }
  },
  methods: {
    handleInput (val) {
      this.handleChange()
    },
    handleChange () {
      this.myprovince = this.handleValue(this.myprovince)
      this.mycity = this.handleValue(this.mycity)
      this.mytext = this.handleValue(this.mytext)
      if (this.mytext) this.mytext = this.mytext.replace(/\s/g, '')
      this.$emit('input', this.myprovince + ' ' + this.mycity + ' ' + this.mytext)
      this.$emit('update:answers', {province: this.myprovince, city: this.mycity, text: this.mytext})
    },
    handleValue (val) {
      if (!val) return ''
      else return val
    }
  }
}
</script>


