import Vue from 'vue'
import VueRouter from 'vue-router'
/** all link */
import App from './App'
import Login from './views/Login'
import Home from './views/Home'
import Notification from './views/Notification.vue'
import Project from './views/Project.vue'
import Record from './views/Record.vue'
import WorkData from './views/Workdata.vue'
import SaveList from './views/saveList.vue'
import CommitList from './views/CommitList.vue'
import BackList from './views/BackList.vue'
import Search from './views/Search.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: App,
      redirect: {name: 'home'}, //  
      meta: {requiresAuth: true, title: ''}
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
      meta: {requiresAuth: true, title: ''}
    },
    {
      path: '/notification',
      component: Notification,
      meta: {requiresAuth: true, title: ''}
    },
    {
      path: '/project/:projectid',
      component: Project,
      meta: {requiresAuth: true, title: ''},
      children: [
        {
          name: 'newrecord',
          path: 'patient/new',
          component: Record,
          meta: {requiresAuth: true, title: ''}
        },
        {
          name: 'record',
          path: 'patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '' }
        },
        {
          name: 'workdata-record',
          path: 'workdata/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '-' }
        },
        {
          name: 'savelist-record',
          path: 'savelist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '-' }
        },
        {
          name: 'commitlist-record',
          path: 'commitlist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '-' }
        },
        {
          name: 'backlist-record',
          path: 'backlist/patient/:mdid',
          component: Record,
          meta: { requiresAuth: true, title: '-' }
        },
        {
          name: 'workdata',
          path: 'workdata',
          component: WorkData,
          meta: {requiresAuth: true, title: ''}
        },
        {
          name: 'savelist',
          path: 'savelist',
          component: SaveList,
          meta: {requiresAuth: true, title: ''}
        },
        {
          path: 'commitlist',
          component: CommitList,
          meta: {requiresAuth: true, title: ''}
        },
        {
          path: 'backlist',
          component: BackList,
          meta: {requiresAuth: true, title: ''}
        },
        {
          name: 'search',
          path: 'search',
          component: Search,
          meta: {requiresAuth: true, title: ''}
        }
      ]
    },
    { path: '/login', component: Login },
    { path: '*', redirect: '/home' }
  ]
})

export default router



